<template>
  <div class="cate-body">
    <!-- 分类 -->
    <div class="fenlei">
      <div class="fenlei-side">
        <ul class="fenlei-left">
          <!-- 动态绑定要展示的类型 -->
          <li :class="{active:activeType1===0}" @click="fetchTypeData2">全部产品</li>
          <!-- 点击的时候循环获取到每一个的id，动态点击得到id -->
          <li
            v-for="item in type1"
            :key="item.id"
            :class="{active:activeType1===item.id}"
            @click="handleTypeClick(item.id)"
          >{{item.typename}}</li>
        </ul>
        <div class="content">
          <img :src="activeTypeObj.thumb" v-if="activeTypeObj" />
          <img class="img2" src="@/styles/img/1、微网站首页_02.jpg" v-else />
          <h3 class="type-title">{{activeTypeObj?activeTypeObj.typename:"全部分类"}}</h3>
          <div class="new-goods">
            <!-- 二级导航的内容 -->
            <a v-for="item in type2" :key="item.id">
              <router-link :to="{name:'list',query:{tid:item.id}}">
                <div class="new-goods-img">
                  <img :src="item.thumb" alt />
                </div>
                <span>{{item.typename}}</span>
              </router-link>
            </a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "CateBody",
  data() {
    return {
      // 分类1
      type1: [],
      // 点击分类默认值
      activeType1: 0,
      // 商品分类2
      type2: []
    };
  },
  components: {
    
  },
  created() {},
  mounted() {
    // 获取一级分类
    this.fetchTypeData1();
    // 获取全部二级分类
    this.fetchTypeData2();
  },
  methods: {
    // 请求一级分类数据 传的pid默认为0
    async fetchTypeData1() {
      let res = await this.$http.get(this.$api.TYPE, { pid: 0 });
      this.type1 = res.data.data;
    },
    // 请求的分类数据2
    async fetchTypeData2() {
      this.activeType1 = 0;
      let res = await this.$http.get(this.$api.TYPE, {
        level: 2,
        sort: "status"
      });
      this.type2 = res.data.data;
    },
    async handleTypeClick(id) {
      // 点击哪一个获取相应的分类下边的商品数据
      this.activeType1 = id;
      let res = await this.$http.get(this.$api.TYPE, { pid: id });
      this.type2 = res.data.data;
    }
  },
  computed: {
    // 计算属性 点击一级导航去获取对应的id
    activeTypeObj() {
      return this.type1.find(v => v.id === this.activeType1);
    }
  }
};
</script>
<style lang='scss' scoped>
@import "styles/category";
.new-goods {
  width: 100%;
  height: auto;
  a {
    display: block;
    width: 1.62rem;
    height: 1.99rem;
    position: relative;
    .new-goods-img {
      width: 1.5rem;
      height: 1.42rem;
      display: block;
      img {
        width: 1.5rem;
        height: 1.42rem;
      }
    }
    span {
      color: black;
      position: absolute;
      left: 0;
      right: 0;
      top: 1.6rem;
      font-size: 0.22rem;
      text-align: center;
    }
  }
}
</style>
