<template>
  <div class="search" @click="$router.push({name:'search'})">
    <div class="search-box">
      <input type="text" placeholder="搜索店内精品" v-focus v-model="value" autocomplete="off"/>
      <span @click="haldelClick">搜索</span>
      <i class="iconfont icon-sousuo"></i>
    </div>
  </div>
</template>

<script>
export default {
  name: "layoutSearch",
  data() {
    return {
      value:'',
      history:[],
    };
  },
  components: {},
  created() {},
  mounted() {
      if (localStorage.getItem('history')){
      this.history = JSON.parse(localStorage.getItem('history'));
      if(this.history.length >10){
          this.history = this.history.slice(0,9);  
        }
    }
    this.sendSearch();
  },
  beforeRouteLeave(to,from,next){
    localStorage.setItem('history',JSON.stringify(this.history));
    next();
  },
  methods: {
    haldelClick(){
      if (this.value === ''){
        this.$toast("请输入搜索的内容")
      }else {
        let index = this.history.indexOf(this.value);
        if(index!==-1){
          this.history.splice(index,1)
        }
        this.history.unshift(this.value);
        this.$router.push({path:'/search'});
        this.$router.push({name:'searchResult',query:{value:this.value}})
      }
    },
      // 给父组件传值
  sendSearch(){
    this.$emit('send',this.history)
  }
  },
};
</script>
<style lang='scss' scoped>
.search {
  width: 100%;
  height: 0.87rem;
  /* margin-top: 0.88rem; */
  border-bottom: 0.01rem solid #ffcb3f;
}
.search-box {
  width: 7.02rem;
  height: 100%;
  margin: 0 auto;
  padding: 0 0.1rem;
  box-sizing: border-box;
  line-height: 0.87rem;
  position: relative;
}
.search-box input {
  width: 6.04rem;
  height: 0.6rem;
  border: 0.01rem solid #d9d9d9;
  outline: none;
  float: left;
  border-radius: 0.3rem;
  margin-top: 0.13rem;
  text-indent: 3em;
}
.search-box span {
  float: right;
  font-size: 0.24rem;
}
.search-box .icon-sousuo {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0.3rem;
  margin-top: auto;
  margin-bottom: auto;
  font-size: 0.24rem;
  color: rgba(0, 0, 0, 0.8);
}
</style>
